@mixin font-h1() {

    @media screen and (min-width: 1880px) {
        font-size: 48px;
    }

    font-size: 42px;

    font-weight: 800;
    font-style: normal;
    line-height: 116%;
}

@mixin font-h2() {
    font-size: 40px;

    font-weight: 800;
    font-style: normal;
    line-height: 130%;
}

@mixin font-h3() {
    font-size: 24px;

    font-weight: 800;
    font-style: normal;
    line-height: 137%;

    @media screen and (min-width: $w576) {
        font-size: 32px;
    }
}

@mixin font-h4() {
    font-size: 24px;
    font-weight: 800;
    font-style: normal;
    line-height: 150%;
}

@mixin font-h5() {
    font-size: 20px;
    font-weight: bold;
    font-style: normal;
    line-height: 160%;
}

@mixin text-style-20-32-700() {
    font-size: 20px;
